
import Vue from 'vue'
import vm from '@/main'
import router from '@/router'
import store from '@/store/store'
import { ACCESS_TOKEN } from '@/config/defaultSettings'


import NProgress from 'nprogress' // progress bar
import 'nprogress/nprogress.css'
const temporaryList = ['passCertificateTemporary','temporaryVisitorReg','passCertificate','passCode'] //需要获取tokend的路由
NProgress.configure({ showSpinner: false }) // NProgress Configuration

router.beforeEach((to, from, next) => {
  NProgress.start() // start progress bar
  if (to.meta.title) {// 路由发生变化修改页面title 
    document.title = to.meta.title
  } 
  if (to.name == '404') {
    next()
  }
  if (to.query.token) {//路由有传入token使用路由的
    Vue.ls.set(ACCESS_TOKEN, to.query.token)
  }
  if (temporaryList.includes(to.name) && !Vue.ls.get(ACCESS_TOKEN)) {//如果是调用临时访客等页面，传入不到token，则调用接口获取
    store.dispatch('user/GetToken').then((res) => {
      Vue.ls.set(ACCESS_TOKEN, res.data.token)
      store.dispatch('user/GetInfo', { token: Vue.ls.get(ACCESS_TOKEN) }).then(() => {
        next()
      }).catch(err => {
        console.log('err: ', err);
        vm.$notify({ type: 'warning', message: '获取用户信息错误',duration:0 })
      })
    }).catch(err => {
      console.log('err: ', err);
    })
  }
  if (Vue.ls.get(ACCESS_TOKEN)) {
    store.dispatch('user/GetInfo',{token:Vue.ls.get(ACCESS_TOKEN)}).then(() => {
      next()
    }).catch(err => {
      console.log('err: ', err);
      vm.$notify({ type: 'warning', message: '获取用户信息错误',duration:0 })
    })
  }else if(!temporaryList.includes(to.name)){
    setTimeout(() => {
      vm.$notify({ type: 'warning', message: '验证失败，无token。',duration:0 })
    }, 100);
  }
})

router.afterEach(() => {
  NProgress.done() // finish progress bar
})
