/* eslint-disable no-undef */

/** 封装的超长加解m方法
 * param {encryptData}  数据加m方式
 * export {decryptData}  数据解m方式
 * const {rsaPubKey} fun 公钥
 * const {rsaPriKey} fun 私钥
 */

const rsaPriKey = ''
const rsaPubKey = ''

export const encryptData = function (str,pubKey) {
  if (pubKey || rsaPubKey) {
    // 对数据进行加m
    let encryptor = new JSEncrypt() // 新建JSEncrypt对象
    encryptor.setPublicKey(pubKey || rsaPubKey) // 设置公钥
    return encryptor.encryptLong(str)
  } else {
    return str
  }
}
export const decryptData = function (str,priKey) {
  if (priKey || rsaPriKey) {
    // 对数据进行解m
    let encryptor = new JSEncrypt() // 新建JSEncrypt对象
    encryptor.setPrivateKey(priKey || rsaPriKey) // 设置私钥
    return encryptor.decryptLong(str)
  } else {
    return str
  }
}
// 加m方法
JSEncrypt.prototype.encryptLong = function (str) {
  try {
    var ct = ''
    // RSA每次加m117bytes，需要辅助方法判断字符串截取位置
    // 1.获取字符串截取点
    var bytes = []
    bytes.push(0)
    var byteNo = 0
    var len, c
    len = str.length
    for (var i = 0; i < len; i++) {
      c = str.charCodeAt(i)
      if (c >= 0x010000 && c <= 0x10ffff) {
        byteNo += 4
      } else if (c >= 0x000800 && c <= 0x00ffff) {
        byteNo += 3
      } else if (c >= 0x000080 && c <= 0x0007ff) {
        byteNo += 2
      } else {
        byteNo += 1
      }
      if (byteNo % 117 >= 114 || byteNo % 117 === 0) {
        bytes.push(i)
        byteNo = 0
      }
    }
    // 2.截取字符串并分段加m
    if (bytes.length > 1) {
      for (var j = 0; j < bytes.length - 1; j++) {
        var subStr
        if (j === 0) {
          subStr = str.substring(0, bytes[j + 1] + 1)
        } else {
          subStr = str.substring(bytes[j] + 1, bytes[j + 1] + 1)
        }
        var t1 = this.encrypt(subStr)
        ct += t1
      }
      if (bytes[bytes.length - 1] !== str.length - 1) {
        var lastStr = str.substring(bytes[bytes.length - 1] + 1)
        ct += this.encrypt(lastStr)
      }
      return ct
    }
    return this.encrypt(str)
  } catch (ex) {
    return false
  }
}
// 解m方法
JSEncrypt.prototype.decryptLong = function (string) {
  try {
    // 加m的数组
    const arr = string.split('=')
    const length = arr.length
    let str = ''
    arr.forEach((item, i) => {
      if (i !== length - 1) {
        // 最后一条数据为null，则需跳过
        str += this.decrypt(item)
      }
    })
    return str
  } catch (ex) {
    return false
  }
}
