
import axios from 'axios'
import vm from '@/main'
import Exif from 'exif-js'
import { ACCESS_TOKEN,ACCESS_TOKEN_TIME } from '@/config/defaultSettings'
import { encryptData,decryptData } from './jsencrypt' //rsa加密方法，可超长
const util = {}

// read env from configs file
// const env = process.env.NODE_ENV// 判断环境： development---->开发环境  production---->生产环境
util.baseUrl = 'http://192.168.150.112:8989/center/app'

util.QR_CODE_LINK_IP = 'http://192.168.55.102:8080'//访问本系统的IP,用于拼接二维码链接

util.http = axios.create({
  baseURL: util.baseUrl,
  timeout: 60000,
  headers: {
    'Content-Type':'application/json;charset=utf-8'
  }
})

// http请求拦截
util.http.interceptors.request.use(
  function (config) {
    // 设置token
    const token = vm.$ls.get(ACCESS_TOKEN)
    if (token) {
      config.headers[ACCESS_TOKEN] = token // 让每个请求携带自定义 token
    }
    return config
  },
  function (error) {
    // 请求失败的处理
    return Promise.reject(error)
  }
)
// http响应拦截
util.http.interceptors.response.use(
  function (res) {
    const token = res.config.headers[ACCESS_TOKEN]
    if (token) {
      vm.$ls.set(ACCESS_TOKEN, token, ACCESS_TOKEN_TIME)
    }
    return res.data
  },
  function (error) {
    // Do something with response error
    if (error.response) {
      if (error.response.status) {
        vm.$notify({
          message:
            error.response.data.message ||
            `${error.response.status}：未知错误`,
          duration: 3000
        })
      }
      return Promise.reject(error)
    } else if (error.message) {
      if (error.message === 'Network Error') {
        vm.$notify('网络连接错误，请检查网络环境！')
      } else if (error.message.indexOf('timeout') >= 0) {
        vm.$notify('网络连接超时，请重试！')
      } else {
        vm.$notify(error.message)
      }
    } else {
      vm.$notify('知识盲区的错误，联系程序员小哥哥吧！')
    }
  }
)

/**
 * 判断上传图片大小
 * 0：符合要求 1:图片太小 2：图片太大
 */
util.isImgChekSize = 0

/**ios图片旋转问题
 * param {File} file 图片文件
 * 返回一个修改后的bse64文件
 */
util.iosRotateImg = function (file) {
  return new Promise((resolve) => {
    let orientation = ''
    //去获取拍照时的信息，解决拍出来的照片旋转问题
    Exif.getData(file, function () {
      orientation = Exif.getTag(this, 'Orientation')
    })
    // 看支持不支持FileReader
    if (!file || !window.FileReader) return
    if (/^image/.test(file.type)) {
      // 创建一个reader
      let reader = new FileReader()
      // 将图片2将转成 base64 格式
      reader.readAsDataURL(file)
      // 读取成功后的回调
      reader.onloadend = function () {
        let result = this.result
        let img = new Image()
        img.src = result
        img.onload = () => {
          resolve(compress(img, orientation))
        }
      }
    }
  })

  //----------------------------------------------
  //
  // 修复上传图片的时候 压缩图片问题
  //
  //----------------------------------------------
  function compress(img, orientation) {
    let canvas = document.createElement('canvas')
    let ctx = canvas.getContext('2d')

    //------------------------------------------
    // 根据相片宽度或者高度压缩图片
    //------------------------------------------
    let ratio = 1
    let ratioWidth = img.width / 1024
    let ratioHeight = img.height / 768
    // 得到最大压缩比
    if (ratioWidth > 1 || ratioHeight > 1) {
      ratio = ratioWidth > ratioHeight ? ratioWidth:ratioHeight
    }

    //------------------------------------------
    // 设置压缩后图片宽度及高度
    //------------------------------------------
    let width = parseInt(img.width/ratio)
    let height = parseInt(img.height/ratio)
    canvas.width = width
    canvas.height = height
    // 设置铺底色
    ctx.fillStyle = '#fff'
    ctx.fillRect(0, 0, canvas.width, canvas.height)
    ctx.drawImage(img, 0, 0, width, height)

    //----------------------------------------------------
    // TODO 如果图片像素大于10000万则使用瓦片绘制 （现在不用）
    //-----------------------------------------------------
    let count
    // 瓦片canvas
    if ((count = (width * height) / 100000000) > 1) {
      let tCanvas = document.createElement('canvas')
      let tctx = tCanvas.getContext('2d')
      // 计算要分成多少块瓦片
      count = ~~(Math.sqrt(count) + 1)
      // 计算每块瓦片的宽和高
      let nw = ~~(width / count)
      let nh = ~~(height / count)
      tCanvas.width = nw
      tCanvas.height = nh
      for (let i = 0; i < count; i++) {
        for (let j = 0; j < count; j++) {
          tctx.drawImage(
            img,
            i * nw * ratio,
            j * nh * ratio,
            nw * ratio,
            nh * ratio,
            0,
            0,
            nw,
            nh
          )
          ctx.drawImage(tCanvas, i * nw, j * nh, nw, nh)
        }
      }
    }

    //------------------------------------------
    // 修复ios上传图片的时候 被旋转的问题
    //------------------------------------------
    ImgTurn(img, orientation, canvas, ctx)

    //------------------------------------------
    // 根据相片占用空间大小压缩
    //------------------------------------------
    // 获得字节数
    var fileLength = parseInt(img.src.length - (img.src.length / 8) * 2)
    // 由字节转换为KB
    var size = (fileLength / 1024).toFixed(2)
    // 检查图片大小
    util.isImgChekSize = 0
    // 如果小于250KB 压缩比为1 1024*768
    var zipratio = 1
    // 计算压缩比
    if ( size < 25 ) {
      // 图片太小
      util.isImgChekSize = 1
    } else if ( size >= 25 && size < 250 ) {
      // 合适图片
      zipratio = 1
    } else if ( size >= 250 && size < 2500 ) {
      // 压缩比
      zipratio = parseInt(2500 / size)/10
    } else if ( size >= 2500 && size < 7500 ) {
      // 固定压缩比0.1
      zipratio = 0.1
    } else if ( size > 7500) {
      // 图片太大
      util.isImgChekSize = 2
    }
    // 获得压缩比
    zipratio = zipratio * ratio > 1 ? 1:zipratio * ratio
    let ndata = canvas.toDataURL('image/jpeg', zipratio)
    return ndata
  }

  //----------------------------------------------
  //
  // 修复上传图片的时候 被旋转的问题
  //
  //----------------------------------------------
  function ImgTurn(img, orientation, canvas, ctx) {
    let width = canvas.width
    let height = canvas.height

    // 修复上传图片的时候 被旋转的问题
    if (orientation != '' && orientation != 1) {
      switch (orientation) {
        case 6: // 需要顺时针（向左）90度旋转
          if (img.width > img.height) {
            let whrate = img.width / img.height
            canvas.width = parseInt(height/whrate)
            canvas.height = height
            ctx.rotate(90 * Math.PI / 180)
            ctx.drawImage(img, 0, 0, canvas.height, parseInt(-canvas.height/whrate))
          }
          break
        case 8: // 需要逆时针（向右）90度旋转
          if (img.width > img.height) {
            canvas.width = height
            canvas.height = width
            ctx.rotate(270 * Math.PI / 180)
            ctx.drawImage(img, -canvas.height, 0, canvas.height, canvas.width)
          }
          break
        case 3: //需要180度旋转
           ctx.rotate(180 * Math.PI / 180);
           ctx.drawImage(img, -canvas.width, -canvas.height, canvas.width, canvas.height)
          break
      }
    }
  }
}

/**时间转换
 * param {Time} fmt 时间的格式
 * 返回格式化后的时间
 */
util.format = function (date, fmt) {
  let o = {
    'M+': date.getMonth() + 1, // 月份
    'd+': date.getDate(), // 日
    // 'h+': date.getHours() % 12 === 0 ? 12 : date.getHours() % 12, // 小时
    'h+': date.getHours(), // 小时
    'H+': date.getHours(), // 小时
    'm+': date.getMinutes(), // 分
    's+': date.getSeconds(), // 秒
    'q+': Math.floor((date.getMonth() + 3) / 3), // 季度
    S: date.getMilliseconds() // 毫秒
  }
  let week = {
    0: '\u65e5',
    1: '\u4e00',
    2: '\u4e8c',
    3: '\u4e09',
    4: '\u56db',
    5: '\u4e94',
    6: '\u516d'
  }
  let time = {
    0: '\u51cc\u6668', // 上午
    1: '\u4e0a\u5348', // 下午
    2: '\u4e0b\u5348', // 晚上
    3: '\u665a\u4e0a' // 凌晨
  }
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(
      RegExp.$1,
      (date.getFullYear() + '').substr(4 - RegExp.$1.length)
    )
  }
  if (/(E+)/.test(fmt)) {
    fmt = fmt.replace(
      RegExp.$1,
      (RegExp.$1.length > 1
        ? RegExp.$1.length > 2
          ? '/u661f/u671f'
          : '/u5468'
        : '') + week[date.getDay() + '']
    )
  }
  if (/(T)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, time[parseInt(date.getHours() / 6)])
  }
  for (let k in o) {
    if (new RegExp('(' + k + ')').test(fmt)) {
      fmt = fmt.replace(
        RegExp.$1,
        RegExp.$1.length === 1 ? o[k] : ('00' + o[k]).substr(('' + o[k]).length)
      )
    }
  }
  return unescape(fmt.replace(/\/u/g, '%u'))
}

/**
 * [isDuringDate 比较当前时间是否在指定时间段内]
 * @author dongsir
 * @DateTime 2022-03-15
 * @version 1.0
 * @param  date  [beginDateStr] [开始时间]
 * @param  date  [endDateStr]  [结束时间]
 * @return  Boolean
 */
util.isDuringDate = (beginDateStr, endDateStr) => {
  let curDate = new Date(), beginDate = new Date(beginDateStr.replace(/-/g, '/')), endDate = new Date(endDateStr.replace(/-/g, '/'))
  return (curDate>=beginDate && curDate<=endDate)
}


/**
 *  ***加密存储数据***
 * 1.获取路由缓存中的参数
 * 2.更新路由中的参数
 * param {Object} route 路由对象
 * param {Time} time 存储的时间
 * */
util.getRouteParams = (route, time) => {
  if (route.params && Object.keys(route.params).length > 0) {
    // 路由有传参则在路由菜单缓存中添加默认值并返回路由传入的参数
    const value = encryptData(JSON.stringify(route.params),'MIGfMA0GCSqGSIb3DQEBAQUAA4GNADCBiQKBgQDFJAKAURd2iNERNtoDeqmcjtxPKrW4F5W+yfyO7FdEx0X8iG4r7HVYvpCvCUNFiR/xXj13pAPxNqaNKhZBHe9OeDhcycAVhMmMyUxa1Ebh3FsT193smDb1w6tBUvRObj5wwiIn+Q83w1+4FPKJfAMisxnj4Xf1525siTz4YahRjQIDAQAB')
    vm.$ls.set(route.name,value,time)
    return route.params
  } else {
    // 路由没有传参则在路由菜单缓存中获取默认值并返回参数
    const value = vm.$ls.get(route.name)
    return JSON.parse(decryptData(value,''))
  }
}
export default util