import vm from '@/main'

import { getUser,getToken } from "@/api/visitor"
const user = {
  namespaced: true,
  state: {
    unitId: '',
    userInfo: {
      openId: '',
      phoneNbr:'',
      name:'',
    }
  },
  mutations: {
    setUserInfo (state, info) {
      vm.$ls.set('userInfo',JSON.stringify(info))
      state.userInfo = JSON.parse(JSON.stringify(info))
      state.unitId = state.userInfo.unitId
    },
  },
  actions: {
    // 获取用户信息
    GetInfo({ commit },token) {
      return new Promise((resolve, reject) => {
        getUser(token).then(res => {
          commit('setUserInfo', {...res.data,phoneNbr:res.data.phone})
          // commit('setUserInfo', {
          //   openId: '',
          //   phoneNbr:'',
          //   name: '',
          //   unitId:'9bd56899267383737782272'
          // })
          resolve(JSON.parse(JSON.stringify(res)))
        }).catch((err) => {
          reject(err)
        })
      })
    },
    // 获取token
    GetToken() {
      return new Promise((resolve,reject) => {
        getToken({appId:"YFYB",tokenType:'VISITOR_'}).then(res => {
          if (res.code == 200) {
            resolve(res)
          } else {
            reject(res)
          }
        }).catch((err) => {
          reject(err)
        })
      })
    }
  }
}

export default user
