import util from '@/libs/util.js'

// --------------------------------------聚翼数据中台API-----------------------------------------
// 获取token
export function getToken (parameter) {
  return util.http({
    url: '/auth/token',
    method: 'post',
    data: parameter
  })
}
// 获取用户信息
export function getUser (parameter) {
  return util.http({
    url: '/auth/getUserInfo',
    method: 'post',
    data: parameter
  })
}
